        font-size: 27px;
        color: #adb5bd;
    }
    h4{
        .desc-dark{
            color: white !important;
        }
        .desc{
            color:#212529;
            font-size: 13px !important;
            font-weight: 600;
           
        }
        span{
            display: flex;
            font-size: 11px;
            color: #adb5bd;
            font-weight: 500;
        }
        
    }
    
}

