.main-content{
    padding-top: 96px;
    padding-left: 270px;
    background-color: #f5f5f7;
    overflow:hidden;
    transition: padding-right 1s ease;
    .slick-slide{
        width: 125px !important;
        margin-right: 7px;
        margin-left: 4px;
    }
    .main-content-wrapper{
        padding-top: 20px;
        .main-content-container{
            margin-left: auto !important;
            margin-right: auto;
            max-width: 960px;
            margin-left: 50px;
            .main-content-left{
                padding-right: 10px;
                padding-left: 10px;
                .stories-bottom-shadow::after{
                    content: "";
                    background: linear-gradient(
                180deg,rgba(0,0,0,.01) 60%,rgba(0,0,0,.9) 100%);
                    width: 100%;
                    height: 200px;
                    position: absolute;
                    bottom: 0;
                    left: 0;
                    z-index: 1;
                    border-radius: 10px;
                }
                .darkModeadd-slider{
                    background-color: #293145 !important;
                }
                .story-item{
                    margin-bottom: 1rem;
                    margin-top: 1rem;
                    position: relative;
                    height: 200px;
                    width: 125px;
                    border-radius: 10px;
                   
                    .add-button-wrap{
                        padding:1rem;
                        text-align: center;
                        width: 100%;
                        position: absolute;
                        bottom: 0;

                        .story-img-down{
                            margin-top: 0 !important;
                        }
                        .add-button{
                            width: 50px;
                            height: 50px;
                            border-radius: 50px;
                            display: inline-block;
                            line-height: 50px;
                            text-align: center;
                            background-color: #fff!important;
                            position: relative;
                            z-index: 10;
                            .add-icon{
                                width: 25px;
                                height: 25px;
                                color: #0d6efd;
                            }
                            .user-profile-pic{
                                width: 100%;
                            }
                        }
                        .story-author{
                            font-size: 14px;
                            position: relative;
                            z-index: 10;
                            color: #eee;
                        }
                    }
                }
                .story-item-add{
                    background-color: #343a40;
                }
                .story-of-user-slide{
                    background-image: url(../../../assets/images/s-2.jpg);
                }
                .story-of-user-slide2{
                    background-image: url(../../../assets/images/s-1.jpg);
                }
                

               

            }
            .main-content-right{
                height: 450px;
                padding-left: 0 !important;
                
            }
        }
    }
}

@media screen and (max-width:1200px) {
    .main-content{
        padding-left: 150px !important;
    }
    // .date-wrapper{
    //     display: none !important;
    // }
    .event-address-main{
        display: none !important;
    }
    .date-wrapper{
        padding: 0.5rem !important;
        h4{
            font-size: 5 !important;
            span{
                font-size: 12px !important;
            }
        }
    }

    .event-info{
        .event{
            font-size: 11px !important;
        }
    }
}

@media screen and (max-width:992px) {
    .main-content{
        padding-left: 0 !important;
        padding-top: 70px !important;
        .main-content-wrapper{
            padding-left: 10px !important;
            padding-right: 10px !important;
        }
    }
}
