.post-with-photo-dark{
    background-color: #293145 !important;
}

.post-with-photo{
    padding: 1.5rem;
    margin-bottom: 1rem;
    width: 100%;
    background-color: #fff;
    position: relative;
    border-radius: 15px;
    .post-with-photo-header{ 
        margin-bottom: 15px;
       
        .post-with-photo-user-photo{
        width: 40px;
        height: 40px;
        border-radius: 50px;
        display: inline-block;
        line-height: 50px;
        text-align: center;
        background-color: #fff!important;
        position: relative;
        z-index: 10;
        display: flex;
        .user-profile-pic{
            border-radius: 50%;
            width: 100%;
        }
    }
    .post-with-photo-header-left{
        .post-with-photo-user-name-dark{
            color: white !important;
        }
        .post-with-photo-user-name{
            color: #212529;
            font-size: 14px;
        }
        span{
            font-weight: 500;
            color:#adb5bd;
            font-size: 12px;
        }
    }

    .post-with-photo-menu{
        width: 40px;
        height: 40px;
        border-radius: 50px;
        background-color: rgb(238, 238, 238);
        display: inline-block;
        display: flex;
        align-items: center;
        justify-content: center;

        svg{
            width: 20px;
            height: 20px;
            color: black;
        }
    }}

    .post-with-photo-quote{
        margin-bottom: 0.5rem;
        p{
            color: #adb5bd;
            font-weight: 500;
            a{
                font-weight: 600;
                margin-left: 5px;
            }
        }
    }

    .post-with-photo-pic{
        margin-bottom: 1rem;
        img{
            border-radius: 0.5rem;
            width: 100%;
        }
    }

    .post-with-photo-footer{
        .copy-link-input{
            position: relative;
            color: #adb5bd;
            font-weight: 600;
            padding: 0.5rem;
            border-radius: 0.3rem;
            font-size: 13px;
            border: none;
            outline: none;
            width: 100%;
            background-color: #eee;
        }
        .copy-link-icon{
            position: absolute;
            right: 28px;
            font-size: 21px;
            top: 127px;
        }
        .copy-link-input-dark{
            background-color: rgb(26, 34, 54) !important;
        }
        .colors-settings{
            display: inline-block;
            padding: 0;
            margin-right: 8px;
            &:last-child{
                margin-right: 0 !important;
            }
            input[type=radio]{
                display: none;
            }
            .colors-settings-bg{
                display: inline-block;
                width: 32px;
                height: 32px;
                border-radius: 50%;
                text-align: center;
                .social-media-Links{
                    width: 20px;
                    height: 20px;
                    vertical-align: -webkit-baseline-middle;
                    color: white;
                }
            }
        }
        .header-share-menu{
            color: #212529;
            font-size: 16px !important;
        }

        .header-share-menu-dark{
            color: white !important;
        }
       
        .likes-icon{
            width: 20px;
            height: 20px;
            margin-right: 5px;
            fill: grey;
        }
        .liked-icon{
            fill: red;
        }
        .comments-icon{
            width: 20px;
            height: 20px;
            margin-right: 5px;
        }
        .likes-count-dark{
            color: white !important;
        }
        .likes-count{
            color: #212529;
            font-weight: 600;
        }

        .share-icon{
            width: 25px;
            height: 25px;
            color: green;
        }
    }
}